import axios, { AxiosInstance, InternalAxiosRequestConfig, AxiosResponse, AxiosError } from 'axios'
import { useAuthStoreWithOut } from '@/store/modules/auth'
import qs from 'qs'
import { config } from './config'
import { ElMessage } from 'element-plus'
import request from '@/config/axios'

const { result_code, unauthorized_code, request_timeout } = config

// axios
const service: AxiosInstance = axios.create({
  baseURL: '/api', // api  base_url
  timeout: request_timeout, // 
  headers: {} // 
})

// request
service.interceptors.request.use(
  (config: InternalAxiosRequestConfig) => {
    const authStore = useAuthStoreWithOut()
    const token = authStore.getToken
    if (token !== '') {
      ;(config.headers as any)[authStore.getTokenKey ?? 'Authorization'] = token // token 
    }
    if (
      config.method === 'post' &&
      (config.headers as any)['Content-Type'] === 'application/x-www-form-urlencoded'
    ) {
      config.data = qs.stringify(config.data)
    }
    // post put 
    if (
      (config.method === 'post' || config.method === 'put') &&
      (config.headers as any)['Content-Type'] === 'application/json'
    ) {
      for (const key in config.data) {
        // 
        if (config.data[key] === '') {
          config.data[key] = null
        }
      }
    }
    // get
    if (config.method === 'get' && config.params) {
      let url = config.url as string
      url += '?'
      const keys = Object.keys(config.params)
      for (const key of keys) {
        if (
          // get
          config.params[key] !== void 0 &&
          config.params[key] !== null &&
          config.params[key] !== ''
        ) {
          url += `${key}=${encodeURIComponent(config.params[key])}&`
        }
      }
      url = url.substring(0, url.length - 1)
      config.params = {}
      config.url = url
    }
    return config
  },
  (error: AxiosError) => {
    // Do something with request error
    console.log('', error) // for debug
    Promise.reject(error)
  }
)

// response 
service.interceptors.response.use(
  (response: AxiosResponse<any>) => {
    // 
    const code = response.data.code || unauthorized_code
    const message = response.data.message || ''
    const refresh = response.headers['if-refresh']

    if (response.config.responseType === 'blob') {
      // 
      return response
    } else if (code === result_code) {
      if (refresh === '1') {
        // tokentoken
        refreshToken().then((res) => {
          const authStore = useAuthStoreWithOut()
          authStore.setToken(`${res.data.token_type} ${res.data.access_token}`)
          authStore.setRefreshToken(res.data.refresh_token)
        })
      }
      return response.data
    } else if (code === unauthorized_code) {
      // tokentoken
      refreshToken().then((res) => {
        const authStore = useAuthStoreWithOut()
        authStore.setToken(`${res.data.token_type} ${res.data.access_token}`)
        authStore.setRefreshToken(res.data.refresh_token)
        ElMessage.error('')
      })
    } else {
      ElMessage.error(message)
    }
  },
  (error: AxiosError) => {
    console.log('err', error)
    let { message } = error
    const authStore = useAuthStoreWithOut()
    const status = error.response?.status
    switch (status) {
      case 400:
        message = ''
        break
      case 401:
        // token
        authStore.logout()
        message = ''
        break
      case 403:
        // 
        authStore.logout()
        message = ''
        break
      case 404:
        message = `: ${error.response?.config.url}`
        break
      case 408:
        message = ''
        break
      case 500:
        message = ''
        break
      case 501:
        message = ''
        break
      case 502:
        message = ''
        break
      case 503:
        message = ''
        break
      case 504:
        message = ''
        break
      case 505:
        message = 'HTTP'
        break
      default:
        break
    }
    ElMessage.error(message)
    return Promise.reject(error)
  }
)

// Token
const refreshToken = (): Promise<IResponse> => {
  const authStore = useAuthStoreWithOut()
  const data = authStore.getRefreshToken
  return request.post({ url: '/auth/token/refresh', data })
}

export { service }
